import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
const COIN_API_URL = "https://rest.coinapi.io/v1/";
const COIN_API_KEY = process.env.REACT_APP_API_KEY_FOR_CHART;
const initialState = {
    history: [],
    status: "idle",
    error: null,
};
export const fetchCurrencyHistory = createAsyncThunk("currency/fetchCurrencyHistory", async ({ currencyCode, dayCount }) => {
    const startDate = getDaysAgo(dayCount);
    const endDate = getToday();
    console.log("Fetching data from:", startDate, "to:", endDate);
    const response = await axios.get(`${COIN_API_URL}exchangerate/USD/${currencyCode}/history`, {
        params: {
            period_id: "1DAY",
            time_start: startDate,
            time_end: endDate,
        },
        headers: {
            "X-CoinAPI-Key": COIN_API_KEY,
            Accept: "application/json",
        },
    });
    console.log("API Response:", response.data);
    return response.data;
});
const currencySlice = createSlice({
    name: "currency",
    initialState,
    reducers: {},
    extraReducers: (builder) => {
        builder
            .addCase(fetchCurrencyHistory.pending, (state) => {
            state.status = "loading";
            state.error = null;
        })
            .addCase(fetchCurrencyHistory.fulfilled, (state, action) => {
            state.status = "succeeded";
            state.history = action.payload;
            console.log("Updated Currency History:", action.payload);
        })
            .addCase(fetchCurrencyHistory.rejected, (state, action) => {
            state.status = "failed";
            state.error = action.error.message || "Failed to fetch data";
        });
    },
});
export default currencySlice.reducer;
function getDaysAgo(days) {
    const date = new Date();
    date.setDate(date.getDate() - days);
    return date.toISOString().split("T")[0];
}
function getToday() {
    return new Date().toISOString().split("T")[0];
}
